import service from './service';
import { AddDiaryItemRequest, DiaryDetail, DiaryListItem, FetchDairyListResponse } from '@src/api/diary.types';

export const fetchDiaryList = (params?: object) => service.get<FetchDairyListResponse>('/essay/list', params);
export const fetchDiaryDetail = (params?: object) => service.get<DiaryDetail>('/essay/item', params);
export const addDiaryItem = (params: AddDiaryItemRequest) => service.post('essay/add', params);