import axios from 'axios';

const baseURL = process.env.NODE_ENV === 'production' ? '/' : 'http://localhost:8088/';

// 创建axios实例
const service = axios.create({
  baseURL,
  headers: {
    'X-Requested-With': 'XMLHttpRequest',
  },
  timeout: 10000, // 请求超时时间
});

const handleError = (response: { errno: number; errmsg: string; }) => {
  const { errno, errmsg } = response;
  switch (errno) {
    default:
      console.log(errmsg);
      break
  }
  return Promise.reject(response)
};

// request拦截器
service.interceptors.request.use(
  // 在发送请求前做什么
  config => {
    if (config.method === 'post') {
      // config.transformRequest = [function (data) {
      //   // 对post传参进行处理，此处未对array、object进行处理，array会使用逗号分隔，object会被处理为[object object];
      //   let newData = '';
      //   // eslint-disable-next-line
      //   for (let k in data) {
      //     newData += `&${encodeURIComponent(k)}=${encodeURIComponent(data[k])}`;
      //   }
      //   if (newData.indexOf('&') === 0) {
      //     newData = newData.substring(1);
      //   }
      //   return newData;
      // }]
    }
    return config;
  },
  error => {
    // 对请求错误做些什么
    console.error(error);
    return Promise.reject(error);
  }
);

// response拦截器
service.interceptors.response.use(response => {
    if (response.data.errno === 0) {
      return response;
    } else {
      return handleError(response.data);
    }
  },
  error => {
    console.error('response_error' + error);
    return Promise.reject(error);
  }
);
interface BaseResponse<T> {
  data: T;
  errno: number;
  message: string;
}
export default {
  post<T> (url: string, data: object) {
    return service.post<BaseResponse<T>>(url, data,{
      headers: {
        'Content-Type': 'application/json',
      },
    })
      .then(res => res.data.data)
      .catch(error => Promise.reject(error));
  },
  get<T> (url: string, params?: object) {
    return service.get<BaseResponse<T>>(url,{
      params,
    })
      .then(res => res.data.data);
  },
};


