import React, { FC, useEffect, useState } from 'react';
import styles from './DiaryList.module.scss';
import { fetchDiaryList } from '@src/api';
import { DiaryListItem } from '@api/diary.types';
import { Avatar, List, Skeleton, Button } from 'antd';
import { RouteComponentProps, withRouter } from 'react-router';

const { Item } = List;
const { Meta } = Item;
const COUNT = 10;

const DiaryList: FC<RouteComponentProps> = ({ history }) => {
  const [list, setList] = useState<DiaryListItem[]>([]);
  const [pageCount, setPageCount] = useState(1);
  const [loading, setLoading] = useState(false);
  const [pageNo, setPageNo ] = useState(1);
  useEffect(() => {
    // setLoading(true);
    fetchDiaryList({
      pageNo,
      pageSize: COUNT,
    })
      .then(res => {
        setPageCount(res.pagination.pageCount);
        setList(res.list);
        setLoading(false);
        // window.dispatchEvent(new Event('resize'));
      });
  }, [pageNo]);

  const onPageChange = (index: number) => {
    setPageNo(pageNo + index);
  };
  const pagination =(
    <div className={styles.pagination}>
      {pageNo > 1 && <Button className={styles.pageBtn} onClick={() => onPageChange(-1)}>← Newer</Button>}
      <span>{pageNo} / {pageCount}</span>
      {pageNo < pageCount && <Button className={styles.pageBtn} onClick={() => onPageChange(1)}>Older →</Button>}
    </div>
  );

  return (
    <List
      className={styles.main}
      itemLayout="horizontal"
      loadMore={pagination}
      dataSource={list}
      renderItem={({ title, date, author, mood, _id }) => (
        <Item>
          <Skeleton avatar title={false} loading={loading} active>
            <Meta
              avatar={<Avatar icon={mood} size={28} className={styles[mood]} />}
              title={<span className={styles.itemText} onClick={() => history.push(`/diary/item/${_id}`)}>{title}</span>}
              description={<span className={styles.itemText}>{author}</span>}
            />
            <time dateTime={date}>{date.slice(0, 10)}</time>
          </Skeleton>
        </Item>
      )}
    />
  );
};

export default withRouter(DiaryList);