import React, { FC, FormEvent, Fragment } from 'react';
import { RouteComponentProps, withRouter } from 'react-router';
import BraftEditor, { ControlType } from 'braft-editor';

import 'braft-editor/dist/index.css';
import styles from './DiaryWrite.module.scss';

import { Button, Form, Icon, Input, Radio } from 'antd';
import { FormComponentProps } from 'antd/lib/form';
import { AddDiaryItemRequest } from '@api/diary.types';
import { addDiaryItem } from '@src/api';

const { Item } = Form;

const controls: ControlType[] = [
  'undo',
  'redo',
  'separator',
  'bold',
  'italic',
  'underline',
  'text-color',
  'separator',
  'emoji',
  'separator',
  'link',
  'separator',
  'media',
  'separator',
  'clear',
];

type Props = FormComponentProps<AddDiaryItemRequest> & RouteComponentProps;
const DiaryWrite: FC<Props> = ({ form: { validateFields, getFieldDecorator }, history }) => {
  const handleSubmit = (e: FormEvent) => {
    e.preventDefault();
    validateFields((err, values) => {
      if (!err) {
        addDiaryItem({
          ...values,
          content: values.content.toHTML(),
        }).then(() => {
          history.replace('/diary/list');
        });
      }
    });
  };

  return (
    <Fragment>
      <Form className={styles.form} onSubmit={handleSubmit} hideRequiredMark>
        <Item label="Author" className={styles.item}>
          {getFieldDecorator('author', {
            rules: [{ required: true, max: 20 }],
          })(<Input type="text" placeholder="author" className={styles.input}/>)}
        </Item>
        <Item label="Title" className={styles.item}>
          {getFieldDecorator('title', {
            rules: [{ required: true, max: 50 }],
          })(<Input type="text" placeholder="title" className={styles.input}/>)}
        </Item>
        <Item label="Content" className={styles.item}>
          {getFieldDecorator('content', {
            rules: [
              {
                required: true,
                validator: (_, value, callback) => {
                  if (value.isEmpty()) {
                    callback('请输入正文内容');
                  } else if (value.toText().length > 5000) {
                    callback('内容不能超过5000字');
                  } else {
                    callback();
                  }
                },
              },
            ],
          })(<BraftEditor className={styles.braftEditor} controls={controls} />)}
        </Item>
        <Item className={styles.item} label="Mood">
          {getFieldDecorator('mood', {
            rules: [{ required: true, message: 'Please choose your mood !' }],
          })(
            <Radio.Group>
              <Radio value="smile" className={styles.radio}>
                <Icon type="smile" /> 开心
              </Radio>
              <Radio value="meh" className={styles.radio}>
                <Icon type="meh" /> 一般
              </Radio>
              <Radio value="frown" className={styles.radio}>
                <Icon type="frown" /> 伤心
              </Radio>
            </Radio.Group>,
          )}
        </Item>
        <Item>
          <Button type="primary" htmlType="submit">
            Submit
          </Button>
        </Item>
      </Form>
    </Fragment>
  );
};

export default withRouter(Form.create()(DiaryWrite));
