import React, { FC, useEffect, useState } from 'react';
import styles from './Diary.module.scss';
import { DiaryDetail } from '@api/diary.types';
import { RouteComponentProps, withRouter } from 'react-router';
import { fetchDiaryDetail } from '@src/api';
import { Icon } from 'antd';

interface RouteProps {
  id: string;
}
type Props = RouteComponentProps<RouteProps>;

const Diary: FC<Props> = ({
  match: {
    params: { id },
  },
}) => {
  const [diary, setDiary] = useState<DiaryDetail>({
    date: '',
    title: '',
    author: '',
    mood: '',
    _id: '',
    content: '',
  });
  useEffect(() => {
    fetchDiaryDetail({
      id,
    }).then(res => {
      setDiary(res);
    });
  }, []);
  const { date, mood, author, title, content } = diary;
  return (
    <div className={styles.main}>
      <h1>{title}</h1>
      <section className={styles.details}>
        <time dateTime={date}>{date.slice(0, 10)}</time>
        <span>{author}</span>
        <Icon type={mood || 'smile'} />
      </section>
      <section className={styles.content} dangerouslySetInnerHTML={{ __html: content }} />
    </div>
  );
};

export default withRouter(Diary);
